  src: url("../fonts/rouble.woff2") format("woff2"), url("../fonts/rouble.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Inter";
  font-display: swap;
  src: url("../fonts/Inter-Regular.woff2") format("woff2"), url("../fonts/Inter-Regular.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Inter";
  font-display: swap;
  src: url("../fonts/Inter-Medium.woff2") format("woff2"), url("../fonts/Inter-Medium.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Inter";
  font-display: swap;
  src: url("../fonts/Inter-SemiBold.woff2") format("woff2"), url("../fonts/Inter-SemiBold.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Inter";
  font-display: swap;
  src: url("../fonts/Inter-Bold.woff2") format("woff2"), url("../fonts/Inter-Bold.woff") format("woff"); }

*,
*::before,
*::after {
  box-sizing: border-box; }

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh; }

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  font-family: "Inter", "Arial", sans-serif;
  color: #2A2A2A;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility; }

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff; }

a {
  color: #000000;
  text-decoration: none; }

img,
svg,
video {
  display: block;
  max-width: 100%;
  height: auto; }

textarea {
  resize: none; }

input,
textarea {
  border-radius: 0; }
  input::placeholder,
  textarea::placeholder {
    opacity: 1; }
  input:invalid,
  textarea:invalid {
    box-shadow: none; }

select {
  border-radius: 0; }

input::-ms-clear, input::-ms-reveal {
  display: none; }

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  appearance: none; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none; }

input[type="number"] {
  appearance: textfield; }

select,
textarea,
input:matches([type="email"],
[type="number"],
[type="password"],
[type="search"],
[type="tel"],
[type="text"],
[type="url"]) {
  appearance: none; }

button,
[type="button"],
[type="reset"],
[type="submit"] {
  appearance: none; }

.rouble {
  font-family: "Rouble", "arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */ }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%); }

.container {
  margin: 0 auto;
  padding: 0 25px;
  max-width: 1440px; }

.page__body {
  position: relative;
  overflow-x: hidden; }

.page__section {
  padding: 150px 0; }
  .page__section:nth-child(even) {
    background-color: #F4F4F4; }

.page__heading {
  margin: 24px 0;
  font-weight: 500;
  font-size: 40px;
  line-height: 55px; }

.page__section-heading {
  margin: 0 0 24px;
  font-weight: 500;
  font-size: 32px;
  line-height: 32px; }

.header {
  display: flex;
  align-items: center;
  background-color: #294570;
  padding: 25px 120px;
  color: #ffffff; }
  @media (max-width: 1599px) {
    .header {
      padding: 25px; } }

.header__logo {
  margin-right: 60px;
  color: #ffffff;
  transition: 0.3s ease; }
  @media (hover: hover) {
    .header__logo:hover:not(.focus-visible) {
      color: #1DAAB3; } }
  .header__logo.focus-visible:focus {
    color: #1DAAB3; }

.header__nav-list {
  display: flex;
  list-style: none;
  padding: 0; }

.header__nav-item a {
  padding: 10px;
  color: inherit;
  margin-right: 72px;
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  transition: 0.3s ease; }
  @media (hover: hover) {
    .header__nav-item a:hover:not(.focus-visible) {
      color: #1DAAB3;
      text-shadow: 0 0 8px rgba(255, 255, 255, 0.3); } }
  .header__nav-item a.focus-visible:focus {
    color: #1DAAB3;
    text-shadow: 0 0 8px rgba(255, 255, 255, 0.3); }

.header__nav-item--current a {
  color: #1DAAB3;
  user-select: none; }

.header__profile {
  margin-left: auto; }

.profile {
  display: flex;
  align-items: center; }

.profile__language-selector {
  list-style: none; }

.profile__language-item {
  font-weight: 700;
  font-size: 18px;
  line-height: 24px; }
  .profile__language-item--checked {
    display: flex;
    align-items: center; }
    .profile__language-item--checked::after {
      margin-left: 6px;
      content: "";
      background-image: url("../img/svg/arrow.svg");
      width: 15px;
      height: 10px; }

.profile__sign-in-button {
  padding: 14px 26px;
  margin-left: 40px;
  border: 1.5px solid #ffffff;
  border-radius: 3px;
  background: none;
  color: #ffffff;
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  opacity: 0.8;
  transition: 0.3s ease; }
  @media (hover: hover) {
    .profile__sign-in-button:hover:not(.focus-visible) {
      opacity: 1; } }
  .profile__sign-in-button.focus-visible:focus {
    opacity: 1; }

.market {
  padding: 63px 0 56px; }

.market__container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 32px;
  align-items: center; }

.market__content {
  padding-right: 200px; }

.market__text {
  margin-bottom: 32px;
  line-height: 28px; }

.button {
  display: inline-block;
  padding: 14px 33px;
  border-radius: 3px;
  background-color: #1DAAB3;
  font-weight: 600;
  text-align: center;
  color: #ffffff;
  transition: 0.3s ease; }
  @media (hover: hover) {
    .button:hover:not(.focus-visible) {
      background-color: rgba(255, 255, 255, 0);
      outline: 1.5px solid #1DAAB3;
      color: #1DAAB3; } }
  .button.focus-visible:focus {
    background-color: rgba(255, 255, 255, 0);
    outline: 1.5px solid #1DAAB3;
    color: #1DAAB3; }

.features__list {
  margin-top: 54px;
  padding: 0;
  display: grid;
  grid-template-columns: 3fr 4fr 3fr;
  list-style: none;
  background-color: #ffffff; }

.features__item:nth-child(1),
.features__item:nth-child(3) {
  border-bottom: 1px solid #D8D8D8; }

.features__item--main {
  grid-column: 2;
  grid-row: 1 / 3;
  border-right: 1px solid #D8D8D8;
  border-left: 1px solid #D8D8D8; }

.feature {
  padding: 44px 32px 34px;
  display: flex;
  flex-direction: column; }

.feature__title {
  margin: 10px 0;
  font-weight: 600;
  font-size: 20px; }

.feature__text {
  margin-top: 0; }

.feature__icon {
  margin-top: auto; }

.feature--main {
  position: relative; }
  .feature--main .feature__icon {
    margin-top: 40px; }
  .feature--main .feature__main-pic {
    position: absolute;
    bottom: 0;
    right: 10%; }

.devices__container {
  display: grid;
  grid-template-columns: 2fr 3fr;
  align-items: center; }

.devices__content {
  padding-right: 50px; }

.devices__text {
  margin-bottom: 42px; }

.faq__list {
  list-style: none;
  background-color: #ffffff;
  padding: 0;
  margin-bottom: 32px; }

.faq__item {
  padding: 19px 30px; }

.faq__header {
  display: flex;
  justify-content: space-between; }

.faq__question {
  margin: 0;
  font-weight: 600;
  font-size: 20px;
  line-height: 52px; }

.faq__checkbox {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  border: none;
  position: relative;
  background-color: #F4F4F4; }
  .faq__checkbox::before {
    position: absolute;
    content: "";
    width: 18px;
    height: 2px;
    background-color: #A0A0A0; }
  .faq__checkbox::after {
    position: absolute;
    content: "";
    width: 2px;
    height: 18px;
    background-color: #A0A0A0; }

.faq__item--opened .faq__checkbox::after {
  display: none; }

.faq__list--nojs .faq__checkbox {
  display: none; }

.faq__answer {
  display: none; }

.faq__item--opened .faq__answer {
  display: block; }

.faq__list--nojs .faq__answer {
  display: block; }

.footer {
  background-color: #294570;
  color: #ffffff;
  padding: 76px 0; }

.footer a {
  color: inherit; }

.footer__top {
  display: grid;
  grid-template-columns: 1fr 2fr 1fr 1fr;
  gap: 32px;
  align-items: center; }

.footer__logo {
  transition: 0.3s ease; }
  @media (hover: hover) {
    .footer__logo:hover:not(.focus-visible) {
      color: #1DAAB3; } }
  .footer__logo.focus-visible:focus {
    color: #1DAAB3; }

.footer__contacts a {
  margin-bottom: 8px;
  transition: 0.3s ease; }
  @media (hover: hover) {
    .footer__contacts a:hover:not(.focus-visible) {
      color: #1DAAB3; } }
  .footer__contacts a.focus-visible:focus {
    color: #1DAAB3; }

.footer__nav {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  margin: 60px 0 90px; }

.footer__nav-column {
  padding: 0;
  list-style: none; }

.footer__nav-item {
  margin-bottom: 8px;
  opacity: 0.7;
  transition: 0.3s ease; }
  @media (hover: hover) {
    .footer__nav-item:hover:not(.focus-visible) {
      color: #1DAAB3; } }
  .footer__nav-item.focus-visible:focus {
    color: #1DAAB3; }
  .footer__nav-item:first-child {
    font-weight: 500;
    font-size: 20px;
    margin-bottom: 20px;
    opacity: 1; }

.footer__nav-column--docs .footer__nav-item {
  text-decoration: underline; }
  .footer__nav-column--docs .footer__nav-item:first-child {
    text-decoration: none; }

.footer__bottom {
  border-top: 1px solid rgba(216, 216, 216, 0.35);
  padding-top: 45px;
  font-size: 14px; }

.apps {
  display: flex; }

.apps__heading {
  font-weight: 400;
  font-size: 16px; }

.apps__list {
  display: flex;
  padding-left: 12px;
  list-style: none; }

.apps__item {
  margin-right: 20px; }

.apps__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 52px;
  height: 52px;
  border-radius: 3px;
  background-color: #ffffff;
  transition: 0.3s ease; }
  @media (hover: hover) {
    .apps__link:hover:not(.focus-visible) {
      background-color: #1DAAB3; } }
  .apps__link.focus-visible:focus {
    background-color: #1DAAB3; }
